import React, { useState, useEffect } from 'react';
import { Layout, Card, Row, Col, Button, Progress, Typography, Space, Statistic } from 'antd';
import { 
  DownloadOutlined, 
  UploadOutlined, 
  WalletOutlined, 
  ThunderboltOutlined,
  NodeIndexOutlined,
  DashboardOutlined
} from '@ant-design/icons';
import TorrentManager from './components/TorrentManager.tsx';
import WalletPanel from './components/WalletPanel.tsx';
import SpeedDashboard from './components/SpeedDashboard.tsx';
import SuperNodePanel from './components/SuperNodePanel.tsx';
import './App.css';

const { Header, Content, Sider } = Layout;
const { Title, Text } = Typography;

interface NetworkStats {
  totalUsers: number;
  totalNodes: number;
  totalData: number;
  avgSpeed: number;
  tokenPrice: number;
}

interface UserStats {
  balance: number;
  staked: number;
  uploaded: number;
  downloaded: number;
  earnings: number;
}

const App: React.FC = () => {
  const [connected] = useState(true); // Mock connection state
  const [activeTab, setActiveTab] = useState('downloads');
  const [networkStats, setNetworkStats] = useState<NetworkStats>({
    totalUsers: 25847,
    totalNodes: 1243,
    totalData: 847.5, // TB
    avgSpeed: 45.2, // MB/s
    tokenPrice: 0.0095, // USD
  });
  
  const [userStats, setUserStats] = useState<UserStats>({
    balance: 1250.5,
    staked: 10000,
    uploaded: 156.8, // GB
    downloaded: 892.4, // GB
    earnings: 12.75, // USD
  });

  const [currentSpeed, setCurrentSpeed] = useState(0);
  const [downloadProgress, setDownloadProgress] = useState(0);

  useEffect(() => {
    // 
    const interval = setInterval(() => {
      setCurrentSpeed(Math.random() * 80 + 20);
      setDownloadProgress(prev => (prev + Math.random() * 5) % 100);
    }, 2000);

    return () => clearInterval(interval);
  }, []);

  const menuItems = [
    { key: 'downloads', icon: <DownloadOutlined />, label: '' },
    { key: 'dashboard', icon: <DashboardOutlined />, label: '' },
    { key: 'wallet', icon: <WalletOutlined />, label: 'TF' },
    { key: 'supernode', icon: <NodeIndexOutlined />, label: '' },
  ];

  const renderContent = () => {
    switch (activeTab) {
      case 'downloads':
        return <TorrentManager userBalance={userStats.balance} />;
      case 'dashboard':
        return <SpeedDashboard currentSpeed={currentSpeed} networkStats={networkStats} />;
      case 'wallet':
        return <WalletPanel userStats={userStats} tokenPrice={networkStats.tokenPrice} />;
      case 'supernode':
        return <SuperNodePanel userStats={userStats} />;
      default:
        return <TorrentManager userBalance={userStats.balance} />;
    }
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      {/* Header */}
      <Header style={{ 
        background: 'linear-gradient(90deg, #1890ff 0%, #722ed1 100%)',
        padding: '0 24px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
      }}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <ThunderboltOutlined style={{ fontSize: '24px', color: 'white', marginRight: '12px' }} />
          <Title level={3} style={{ color: 'white', margin: 0 }}>
            ThunderFuel Network
          </Title>
        </div>
        
        <Space>
          <Text style={{ color: 'white' }}>
            : {networkStats.avgSpeed} MB/s
          </Text>
          <Text style={{ color: 'white' }}>
            TF: ${networkStats.tokenPrice}
          </Text>
          <Button type="primary" ghost>
             ()
          </Button>
        </Space>
      </Header>

      <Layout>
        {/* Sidebar */}
        <Sider width={250} style={{ background: '#f0f2f5' }}>
          <div style={{ padding: '16px' }}>
            {/*  */}
            <Card size="small" style={{ marginBottom: '16px' }}>
              <Space direction="vertical" style={{ width: '100%' }}>
                <Statistic
                  title=""
                  value={networkStats.totalUsers}
                  suffix=""
                  valueStyle={{ fontSize: '14px' }}
                />
                <Statistic
                  title=""
                  value={networkStats.totalNodes}
                  suffix=""
                  valueStyle={{ fontSize: '14px' }}
                />
                <Statistic
                  title=""
                  value={networkStats.totalData}
                  suffix="TB"
                  valueStyle={{ fontSize: '14px' }}
                />
              </Space>
            </Card>

            {/*  */}
            {connected && (
              <Card size="small" style={{ marginBottom: '16px' }}>
                <Space direction="vertical" style={{ width: '100%' }}>
                  <Statistic
                    title="TF"
                    value={userStats.balance}
                    suffix="TF"
                    valueStyle={{ fontSize: '14px', color: '#1890ff' }}
                  />
                  <Statistic
                    title=""
                    value={userStats.earnings}
                    prefix="$"
                    valueStyle={{ fontSize: '14px', color: '#52c41a' }}
                  />
                  <Progress
                    type="circle"
                    size={60}
                    percent={Math.round((userStats.uploaded / (userStats.uploaded + userStats.downloaded)) * 100)}
                    format={() => ''}
                  />
                </Space>
              </Card>
            )}

            {/*  */}
            <div>
              {menuItems.map(item => (
                <Button
                  key={item.key}
                  type={activeTab === item.key ? 'primary' : 'text'}
                  icon={item.icon}
                  style={{ 
                    width: '100%', 
                    textAlign: 'left', 
                    marginBottom: '8px',
                    height: '40px'
                  }}
                  onClick={() => setActiveTab(item.key)}
                >
                  {item.label}
                </Button>
              ))}
            </div>

            {/*  */}
            {connected && (
              <Card 
                size="small" 
                style={{ 
                  marginTop: '16px',
                  background: 'linear-gradient(45deg, #ff4d4f, #ff7a45)',
                  color: 'white'
                }}
              >
                <Space direction="vertical" style={{ width: '100%' }}>
                  <Text style={{ color: 'white', fontWeight: 'bold' }}>
                    
                  </Text>
                  <Button 
                    type="primary" 
                    danger 
                    icon={<ThunderboltOutlined />}
                    style={{ width: '100%' }}
                    disabled={userStats.balance < 10}
                  >
                    10TF
                  </Button>
                  <Text style={{ color: 'white', fontSize: '12px' }}>
                    300%
                  </Text>
                </Space>
              </Card>
            )}
          </div>
        </Sider>

        {/* Main Content */}
        <Layout style={{ padding: '24px' }}>
          <Content>
            {!connected ? (
              // 
              <div style={{ 
                textAlign: 'center', 
                padding: '100px 20px',
                background: 'white',
                borderRadius: '8px'
              }}>
                <ThunderboltOutlined style={{ fontSize: '72px', color: '#1890ff' }} />
                <Title level={1}> ThunderFuel</Title>
                <Title level={3} type="secondary">
                  P2P
                </Title>
                <Space direction="vertical" size="large" style={{ marginTop: '40px' }}>
                  <Row gutter={24} justify="center">
                    <Col span={6}>
                      <Card>
                        <Statistic
                          title=""
                          value={371}
                          suffix="%"
                          valueStyle={{ color: '#cf1322' }}
                        />
                      </Card>
                    </Col>
                    <Col span={6}>
                      <Card>
                        <Statistic
                          title=""
                          value={2.5}
                          prefix="$"
                          suffix="/"
                          valueStyle={{ color: '#3f8600' }}
                        />
                      </Card>
                    </Col>
                    <Col span={6}>
                      <Card>
                        <Statistic
                          title=""
                          value={networkStats.totalNodes}
                          suffix=""
                          valueStyle={{ color: '#1890ff' }}
                        />
                      </Card>
                    </Col>
                  </Row>
                  
                  <div style={{ marginTop: '40px' }}>
                    <Text style={{ fontSize: '16px', marginBottom: '20px', display: 'block' }}>
                      
                    </Text>
                    <Button type="primary" size="large">
                       ()
                    </Button>
                  </div>
                </Space>
              </div>
            ) : (
              renderContent()
            )}
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
};

export default App;
