import React from 'react';
import { Card, Row, Col, Statistic, Progress, Typography, Space } from 'antd';

const { Title, Text } = Typography;

interface Props {
  currentSpeed: number;
  networkStats: {
    totalUsers: number;
    totalNodes: number;
    totalData: number;
    avgSpeed: number;
  };
}

const SpeedDashboard: React.FC<Props> = ({ currentSpeed, networkStats }) => {
  return (
    <div>
      <Title level={2}></Title>
      
      {/*  */}
      <Row gutter={16} style={{ marginBottom: '24px' }}>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={currentSpeed.toFixed(1)}
              suffix="MB/s"
              valueStyle={{ color: '#1890ff', fontSize: '32px' }}
            />
            <Progress 
              percent={Math.min((currentSpeed / 100) * 100, 100)} 
              strokeColor="#1890ff"
              showInfo={false}
            />
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={(currentSpeed * 0.3).toFixed(1)}
              suffix="MB/s"
              valueStyle={{ color: '#52c41a', fontSize: '32px' }}
            />
            <Progress 
              percent={Math.min(((currentSpeed * 0.3) / 30) * 100, 100)}
              strokeColor="#52c41a"
              showInfo={false}
            />
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={networkStats.avgSpeed}
              suffix="MB/s"
              valueStyle={{ color: '#722ed1', fontSize: '32px' }}
            />
            <Text type="secondary">
              {currentSpeed > networkStats.avgSpeed ? '' : ''}
            </Text>
          </Card>
        </Col>
      </Row>

      {/*  -  */}
      <Card title="" style={{ marginBottom: '24px' }}>
        <div style={{ padding: '20px 0' }}>
          {[
            { time: '30', speed: 25 },
            { time: '25', speed: 30 },
            { time: '20', speed: 28 },
            { time: '15', speed: 45 },
            { time: '10', speed: 52 },
            { time: '5', speed: 48 },
            { time: '', speed: currentSpeed },
          ].map((item, index) => (
            <div key={index} style={{ marginBottom: '10px' }}>
              <Row justify="space-between" align="middle">
                <Text>{item.time}</Text>
                <div style={{ flex: 1, margin: '0 20px' }}>
                  <Progress 
                    percent={(item.speed / 80) * 100} 
                    showInfo={false}
                    strokeColor={index === 6 ? "#722ed1" : "#1890ff"}
                  />
                </div>
                <Text strong style={{ color: index === 6 ? "#722ed1" : "#1890ff" }}>
                  {item.speed.toFixed(1)} MB/s
                </Text>
              </Row>
            </div>
          ))}
        </div>
      </Card>

      {/*  */}
      <Row gutter={16}>
        <Col span={12}>
          <Card title="">
            <Space direction="vertical" style={{ width: '100%' }}>
              <div>
                <Text></Text>
                <Progress percent={96} strokeColor="#52c41a" />
              </div>
              <div>
                <Text></Text>
                <Progress percent={99} strokeColor="#1890ff" />
              </div>
              <div>
                <Text></Text>
                <Progress percent={94} strokeColor="#722ed1" />
              </div>
            </Space>
          </Card>
        </Col>
        <Col span={12}>
          <Card title="">
            <Space direction="vertical">
              <Text> </Text>
              <Text> TF</Text>
              <Text> </Text>
              <Text> </Text>
            </Space>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default SpeedDashboard;
