import React, { useState } from 'react';
import { 
  Card, 
  Row, 
  Col, 
  Statistic, 
  Button, 
  Space, 
  Typography, 
  Switch,
  Progress,
  Modal,
  Input,
  message,
  Alert
} from 'antd';
import { 
  NodeIndexOutlined, 
  DollarOutlined, 
  ThunderboltOutlined,
  SettingOutlined,
  PlayCircleOutlined,
  PauseOutlined
} from '@ant-design/icons';

const { Title, Text } = Typography;

interface Props {
  userStats: {
    balance: number;
    staked: number;
    uploaded: number;
    downloaded: number;
    earnings: number;
  };
}

const SuperNodePanel: React.FC<Props> = ({ userStats }) => {
  const [isNodeRunning, setIsNodeRunning] = useState(false);
  const [showStakeModal, setShowStakeModal] = useState(false);
  const [stakeAmount, setStakeAmount] = useState('');

  // 
  const nodeStats = {
    uptime: 98.5, // 
    bandwidth: 156.8, // Mbps
    connections: 45, // 
    dataServed: 892.4, // GB
    hoursOnline: 168, // 
    earnings: 840, // TF
  };

  const handleStake = () => {
    const amount = parseFloat(stakeAmount);
    if (!amount || amount < 10000) {
      message.error(' 10,000 TF');
      return;
    }
    
    if (amount > userStats.balance) {
      message.error('');
      return;
    }
    
    message.success(` ${amount} TF`);
    setShowStakeModal(false);
    setStakeAmount('');
  };

  const toggleNode = () => {
    if (!isNodeRunning) {
      if (userStats.staked < 10000) {
        message.error(' 10,000 TF ');
        return;
      }
      message.success('');
    } else {
      message.info('');
    }
    setIsNodeRunning(!isNodeRunning);
  };

  return (
    <div>
      <Title level={2}></Title>
      
      {/*  */}
      <Row gutter={16} style={{ marginBottom: '24px' }}>
        <Col span={6}>
          <Card>
            <Statistic
              title=""
              value={userStats.staked}
              suffix="TF"
              valueStyle={{ 
                color: userStats.staked >= 10000 ? '#52c41a' : '#ff4d4f',
                fontSize: '24px'
              }}
              prefix={<NodeIndexOutlined />}
            />
            <Text type="secondary">
              {userStats.staked >= 10000 ? '' : ' 10,000 TF'}
            </Text>
          </Card>
        </Col>
        <Col span={6}>
          <Card>
            <Statistic
              title=""
              value={nodeStats.earnings}
              suffix="TF"
              valueStyle={{ color: '#722ed1', fontSize: '24px' }}
              prefix={<DollarOutlined />}
            />
            <Text type="secondary"></Text>
          </Card>
        </Col>
        <Col span={6}>
          <Card>
            <Statistic
              title=""
              value={nodeStats.hoursOnline}
              suffix=""
              valueStyle={{ color: '#1890ff', fontSize: '24px' }}
            />
            <Text type="secondary"></Text>
          </Card>
        </Col>
        <Col span={6}>
          <Card>
            <Statistic
              title=""
              value={nodeStats.dataServed}
              suffix="GB"
              valueStyle={{ color: '#fa8c16', fontSize: '24px' }}
            />
            <Text type="secondary"></Text>
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Card style={{ marginBottom: '24px' }}>
        <Row gutter={16} align="middle">
          <Col span={8}>
            <Space>
              <Text strong>:</Text>
              <Switch
                checked={isNodeRunning}
                onChange={toggleNode}
                checkedChildren=""
                unCheckedChildren=""
                disabled={userStats.staked < 10000}
              />
              {isNodeRunning ? (
                <PlayCircleOutlined style={{ color: '#52c41a', fontSize: '20px' }} />
              ) : (
                <PauseOutlined style={{ color: '#ff4d4f', fontSize: '20px' }} />
              )}
            </Space>
          </Col>
          <Col span={8}>
            <Space>
              <Button 
                type="primary" 
                icon={<ThunderboltOutlined />}
                disabled={userStats.staked >= 10000}
                onClick={() => setShowStakeModal(true)}
              >
                {userStats.staked >= 10000 ? '' : ''}
              </Button>
              <Button icon={<SettingOutlined />}>
                
              </Button>
            </Space>
          </Col>
          <Col span={8}>
            <Text>
              ID: {isNodeRunning ? 'TF-NODE-7B3F9A21' : ''}
            </Text>
          </Col>
        </Row>
      </Card>

      {/*  */}
      {userStats.staked >= 10000 && (
        <Row gutter={16} style={{ marginBottom: '24px' }}>
          <Col span={12}>
            <Card title="">
              <Space direction="vertical" style={{ width: '100%' }}>
                <div>
                  <Text>: {nodeStats.uptime}%</Text>
                  <Progress 
                    percent={nodeStats.uptime} 
                    strokeColor={nodeStats.uptime >= 95 ? '#52c41a' : '#fa8c16'}
                  />
                </div>
                <div>
                  <Text>: 75%</Text>
                  <Progress percent={75} strokeColor="#1890ff" />
                </div>
                <div>
                  <Text>: </Text>
                  <Progress percent={92} strokeColor="#722ed1" />
                </div>
              </Space>
            </Card>
          </Col>
          <Col span={12}>
            <Card title="">
              <Space direction="vertical" style={{ width: '100%' }}>
                <Row justify="space-between">
                  <Text>:</Text>
                  <Text strong style={{ color: '#52c41a' }}>5 TF</Text>
                </Row>
                <Row justify="space-between">
                  <Text>:</Text>
                  <Text strong style={{ color: '#52c41a' }}>120 TF</Text>
                </Row>
                <Row justify="space-between">
                  <Text>:</Text>
                  <Text strong style={{ color: '#52c41a' }}>3,600 TF</Text>
                </Row>
                <Row justify="space-between">
                  <Text>:</Text>
                  <Text strong style={{ color: '#722ed1' }}>43.2%</Text>
                </Row>
              </Space>
            </Card>
          </Col>
        </Row>
      )}

      {/*  */}
      {userStats.staked < 10000 && (
        <Alert
          message=""
          description={
            <Space direction="vertical">
              <Text> : 10,000 TF</Text>
              <Text> : 100 Mbps </Text>
              <Text> : 3,600 TF (43.2% )</Text>
              <Text> : 95% </Text>
            </Space>
          }
          type="info"
          showIcon
          style={{ marginBottom: '24px' }}
        />
      )}

      {/*  */}
      <Card title="">
        <div style={{ marginBottom: '16px' }}>
          <Row>
            <Col span={8}><Text strong>ID</Text></Col>
            <Col span={4}><Text strong></Text></Col>
            <Col span={4}><Text strong></Text></Col>
            <Col span={4}><Text strong></Text></Col>
            <Col span={4}><Text strong></Text></Col>
          </Row>
        </div>
        {[
          { id: 'TF-A1B2C3', uptime: 99.8, served: '2.5TB', earnings: '4200 TF', rank: 1 },
          { id: 'TF-D4E5F6', uptime: 99.2, served: '2.1TB', earnings: '3900 TF', rank: 2 },
          { id: 'TF-G7H8I9', uptime: 98.9, served: '1.9TB', earnings: '3700 TF', rank: 3 },
          { id: 'TF-J0K1L2', uptime: 98.5, served: '1.8TB', earnings: '3600 TF', rank: 4 },
          { id: 'YOUR-NODE', uptime: nodeStats.uptime, served: `${nodeStats.dataServed}GB`, earnings: `${nodeStats.earnings} TF`, rank: 15 },
        ].map((node, index) => (
          <Row 
            key={node.id} 
            style={{ 
              padding: '8px 0', 
              background: node.id === 'YOUR-NODE' ? '#f0f9ff' : 'transparent',
              borderRadius: '4px'
            }}
          >
            <Col span={8}>
              <Text style={{ fontWeight: node.id === 'YOUR-NODE' ? 'bold' : 'normal' }}>
                {node.id}
              </Text>
            </Col>
            <Col span={4}><Text>{node.uptime}%</Text></Col>
            <Col span={4}><Text>{node.served}</Text></Col>
            <Col span={4}><Text style={{ color: '#52c41a' }}>{node.earnings}</Text></Col>
            <Col span={4}>
              <Text style={{ fontWeight: 'bold', color: '#722ed1' }}>
                #{node.rank}
              </Text>
            </Col>
          </Row>
        ))}
      </Card>

      {/*  */}
      <Modal
        title=" TF "
        open={showStakeModal}
        onOk={handleStake}
        onCancel={() => setShowStakeModal(false)}
        okText=""
        cancelText=""
      >
        <Space direction="vertical" style={{ width: '100%' }}>
          <Alert
            message=""
            description=" TF 7"
            type="warning"
            style={{ marginBottom: '16px' }}
          />
          <div>
            <Text></Text>
            <Input
              placeholder=" 10,000 TF"
              suffix="TF"
              value={stakeAmount}
              onChange={(e) => setStakeAmount(e.target.value)}
            />
          </div>
          <Text type="secondary">
            : {userStats.balance.toFixed(1)} TF
          </Text>
          <Text type="secondary">
            : {stakeAmount ? (parseFloat(stakeAmount || '0') * 0.036).toFixed(0) : '0'} TF
          </Text>
        </Space>
      </Modal>
    </div>
  );
};

export default SuperNodePanel;
