import React, { useState } from 'react';
import { 
  Card, 
  Row, 
  Col, 
  Statistic, 
  Button, 
  Space, 
  Typography, 
  Table, 
  Tag,
  Modal,
  Input,
  message
} from 'antd';
import { 
  WalletOutlined, 
  SendOutlined, 
  SwapOutlined,
  GiftOutlined,
  DollarOutlined
} from '@ant-design/icons';

const { Title, Text } = Typography;

interface Props {
  userStats: {
    balance: number;
    staked: number;
    uploaded: number;
    downloaded: number;
    earnings: number;
  };
  tokenPrice: number;
}

interface Transaction {
  id: string;
  type: 'earn' | 'spend' | 'transfer';
  amount: number;
  description: string;
  time: string;
  status: 'completed' | 'pending';
}

const WalletPanel: React.FC<Props> = ({ userStats, tokenPrice }) => {
  const [showTransferModal, setShowTransferModal] = useState(false);
  const [showExchangeModal, setShowExchangeModal] = useState(false);
  const [transferAmount, setTransferAmount] = useState('');
  const [transferAddress, setTransferAddress] = useState('');
  const [exchangeAmount, setExchangeAmount] = useState('');

  const transactions: Transaction[] = [
    {
      id: '1',
      type: 'earn',
      amount: 16.4,
      description: '',
      time: '10',
      status: 'completed'
    },
    {
      id: '2',
      type: 'earn',
      amount: 2.5,
      description: '',
      time: '1',
      status: 'completed'
    },
    {
      id: '3',
      type: 'spend',
      amount: -10,
      description: '',
      time: '2',
      status: 'completed'
    },
    {
      id: '4',
      type: 'earn',
      amount: 50,
      description: '',
      time: '',
      status: 'completed'
    }
  ];

  const handleTransfer = () => {
    if (!transferAmount || !transferAddress) {
      message.error('');
      return;
    }
    
    const amount = parseFloat(transferAmount);
    if (amount > userStats.balance) {
      message.error('');
      return;
    }
    
    message.success(` ${amount} TF`);
    setShowTransferModal(false);
    setTransferAmount('');
    setTransferAddress('');
  };

  const handleExchange = () => {
    if (!exchangeAmount) {
      message.error('');
      return;
    }
    
    const amount = parseFloat(exchangeAmount);
    if (amount > userStats.balance) {
      message.error('TF');
      return;
    }
    
    if (amount < 100) {
      message.error('100 TF');
      return;
    }
    
    const usdValue = amount * tokenPrice;
    message.success(` ${amount} TF  $${usdValue.toFixed(2)}`);
    setShowExchangeModal(false);
    setExchangeAmount('');
  };

  const transactionColumns = [
    {
      title: '',
      dataIndex: 'type',
      key: 'type',
      render: (type: string) => {
        const config = {
          earn: { color: 'green', text: '' },
          spend: { color: 'red', text: '' },
          transfer: { color: 'blue', text: '' }
        };
        return <Tag color={config[type as keyof typeof config].color}>
          {config[type as keyof typeof config].text}
        </Tag>;
      }
    },
    {
      title: '',
      dataIndex: 'amount',
      key: 'amount',
      render: (amount: number) => (
        <Text style={{ 
          color: amount > 0 ? '#52c41a' : '#ff4d4f',
          fontWeight: 'bold'
        }}>
          {amount > 0 ? '+' : ''}{amount} TF
        </Text>
      )
    },
    {
      title: '',
      dataIndex: 'description',
      key: 'description',
    },
    {
      title: '',
      dataIndex: 'time',
      key: 'time',
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (status: string) => (
        <Tag color={status === 'completed' ? 'green' : 'orange'}>
          {status === 'completed' ? '' : ''}
        </Tag>
      )
    }
  ];

  return (
    <div>
      <Title level={2}>TF </Title>
      
      {/*  */}
      <Row gutter={16} style={{ marginBottom: '24px' }}>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={userStats.balance}
              suffix="TF"
              valueStyle={{ color: '#1890ff', fontSize: '28px' }}
              prefix={<WalletOutlined />}
            />
            <Text type="secondary">
               ${(userStats.balance * tokenPrice).toFixed(2)} USD
            </Text>
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={userStats.staked}
              suffix="TF"
              valueStyle={{ color: '#722ed1', fontSize: '28px' }}
            />
            <Text type="secondary"></Text>
          </Card>
        </Col>
        <Col span={8}>
          <Card>
            <Statistic
              title=""
              value={userStats.earnings}
              prefix="$"
              valueStyle={{ color: '#52c41a', fontSize: '28px' }}
            />
            <Text type="secondary">
              +{(userStats.earnings / tokenPrice).toFixed(1)} TF
            </Text>
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Card style={{ marginBottom: '24px' }}>
        <Title level={4}></Title>
        <Row gutter={16}>
          <Col span={6}>
            <Button 
              type="primary" 
              icon={<SendOutlined />} 
              block
              onClick={() => setShowTransferModal(true)}
            >
               TF
            </Button>
          </Col>
          <Col span={6}>
            <Button 
              icon={<DollarOutlined />} 
              block
              onClick={() => setShowExchangeModal(true)}
            >
              
            </Button>
          </Col>
          <Col span={6}>
            <Button 
              icon={<GiftOutlined />} 
              block
            >
              
            </Button>
          </Col>
          <Col span={6}>
            <Button 
              icon={<SwapOutlined />} 
              block
            >
               TF
            </Button>
          </Col>
        </Row>
      </Card>

      {/*  */}
      <Row gutter={16} style={{ marginBottom: '24px' }}>
        <Col span={12}>
          <Card title="">
            <Space direction="vertical" style={{ width: '100%' }}>
              <Space style={{ justifyContent: 'space-between', width: '100%' }}>
                <Text>1 TF = ${tokenPrice} USD</Text>
                <Tag color="green">+2.3%</Tag>
              </Space>
              <Space style={{ justifyContent: 'space-between', width: '100%' }}>
                <Text></Text>
                <Text>1 TF = $0.01</Text>
              </Space>
              <Space style={{ justifyContent: 'space-between', width: '100%' }}>
                <Text>OTC </Text>
                <Text>1 TF = $0.009</Text>
              </Space>
            </Space>
          </Card>
        </Col>
        <Col span={12}>
          <Card title="">
            <Space direction="vertical">
              <Text> 100 TF</Text>
              <Text> 5</Text>
              <Text> 1%24</Text>
              <Text> </Text>
            </Space>
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Card title="">
        <Table
          columns={transactionColumns}
          dataSource={transactions}
          rowKey="id"
          pagination={{ pageSize: 10 }}
          size="small"
        />
      </Card>

      {/*  */}
      <Modal
        title=" TF"
        open={showTransferModal}
        onOk={handleTransfer}
        onCancel={() => setShowTransferModal(false)}
        okText=""
        cancelText=""
      >
        <Space direction="vertical" style={{ width: '100%' }}>
          <div>
            <Text></Text>
            <Input
              placeholder=""
              value={transferAddress}
              onChange={(e) => setTransferAddress(e.target.value)}
            />
          </div>
          <div>
            <Text></Text>
            <Input
              placeholder=""
              suffix="TF"
              value={transferAmount}
              onChange={(e) => setTransferAmount(e.target.value)}
            />
          </div>
          <Text type="secondary">
            : {userStats.balance.toFixed(1)} TF
          </Text>
        </Space>
      </Modal>

      {/*  */}
      <Modal
        title=""
        open={showExchangeModal}
        onOk={handleExchange}
        onCancel={() => setShowExchangeModal(false)}
        okText=""
        cancelText=""
      >
        <Space direction="vertical" style={{ width: '100%' }}>
          <div>
            <Text></Text>
            <Input
              placeholder="TF"
              suffix="TF"
              value={exchangeAmount}
              onChange={(e) => setExchangeAmount(e.target.value)}
            />
          </div>
          {exchangeAmount && (
            <Text>
              : ${(parseFloat(exchangeAmount || '0') * tokenPrice).toFixed(2)} USD
            </Text>
          )}
          <Text type="secondary">
            : 100 TF
          </Text>
          <Text type="secondary">
            : {userStats.balance.toFixed(1)} TF
          </Text>
        </Space>
      </Modal>
    </div>
  );
};

export default WalletPanel;
